<template>
    <div id="lyric">
<!--{{lyric.lyric}}-->
        <ul :style="{ top: top + 'px' }" id="lyriclist">
            <li v-for="(item,index) in newLyric">
                {{item[1]}}
            </li>
        </ul>

    <audio :src="mp3" id="audio" autoplay="autoplay" controls="controls" preload="auto" style="display: none" ></audio>


    </div>
</template>

<script>
    import  axios from 'axios'
    export default{
        data(){
            return {
                lyric :{
                    "lyric": "[00:00.00]  : \n[00:01.00]"
                },
                newLyric:null,
                mp3:"http://192.168.1.2:302/gbqq.mp3",
                top:200
            }
        },
        created(){

        },
        //keep-alive
        activated(){
            this.ajaxLyric();//

            console.info("------------: activated----------");

        },
        mounted(){
            this. ajaxLyric();

        },
        deactivated: function () {
            console.log(" deactivated")
        },

        methods: {
            //
            ajaxLyric () {
                axios.get(apiurl.getLyric(418603077))
                    .then(function (res) {
                        this.lyric.lyric = (res.data.lrc.lyric);
                        this.newLyric=this.parseLyric(this.lyric.lyric);
                        this.scrollLyrics();
                    }.bind(this))
                    .catch(function (error) {
                        console.log(error);
                    })
            },
            //
            parseLyric (text) {
                //
                var lines = text.split('\n'),
                    //[xx:xx.xx]
                    pattern = /\[\d{2}:\d{2}.\d{2}\]/g,
                    //
                    result = [];
                //
                while (!pattern.test(lines[0])) {
                    lines = lines.slice(1);
                }
                //'\n'
                lines[lines.length - 1].length === 0 && lines.pop();
                lines.forEach(function (v /**/, i /**/, a /**/) {
                    //[xx:xx.xx]
                    var time = v.match(pattern),
                        //
                        value = v.replace(pattern, '');
                    //time[xx:xx.xx][xx:xx.xx][xx:xx.xx]
                    time.forEach(function (v1, i1, a1) {
                        //xx:xx.xx
                        var t = v1.slice(1, -1).split(':');
                        //
                        result.push([parseInt(t[0], 10) * 60 + parseFloat(t[1]), value]);
                    });
                });
                //
                result.sort(function (a, b) {
                    return a[0] - b[0];
                });

                return result;
            },
            scrollLyrics () {
                var audio = document.querySelector('#audio');
                var uls=document.getElementById("lyriclist");
                var lis=uls.getElementsByTagName("li");
                var _this = this;



                audio.addEventListener("timeupdate", function () {

                    for (var i = 0, l =  _this.newLyric.length; i < l; i++) {
                        if (this.currentTime  >  _this.newLyric[i][0]) {
                            _this.top=-i*25;
                            lis[i].className="current";

                        }else{
                        }
                    }
                });
            }
        },
        components: {}
    }
</script>
<style scoped>
    /*lyric*/
    #lyric {
        width:100%;
        height: 150px;
        overflow: hidden;
        color: #fff;
        font-size: 14px;
        overflow: hidden;
        position: relative;
        text-align: center;
        box-sizing: border-box;
        padding:20px 0;
        float: left;
        margin-bottom:10px;
        margin-top:100px;
    }

    #lyric ul {
        position: absolute;
        width:100%;
    }

    #lyric ul li {
        text-align: center;
        height: 25px;
        line-height: 25px;
        color: #333;
    }
    #lyric ul li.current{
        color:  #cb5d5d;
        background: rgba(151,151,145,0.7);
    }
</style>



