<template>
    <div id="m-box">
        <div id="m-bg" :style="{backgroundImage: 'url(' + music.pic + ')'}">
          <!--  {{lyric.lyric}}-->
           <!-- {{newLyric}}-->
        </div>
        <div class="m-circle">
            <img class="rat-img roation" :src="music.pic"/>
        </div>

        <div id="lyric">

            <ul :style="{ top: top + 'px' }" id="lyriclist">
                <li v-for="(item,index) in newLyric">
                    {{item[1]}}
                </li>
            </ul>

        </div> 

        <div id="audio-play">
            <div class="progressbar">
                <div id="current_line" :style="{ width:  currentTime*100/playTime + '%' }"></div>
            </div>
            <audio id="audio" :src="music.url"></audio>
           <!-- <audio id="audio" src="http://code.it919.cn/gbqq.mp3"></audio>-->
            <p><i class="fl">{{ currentTime | minutes}}</i><i class="fr">{{playTime | minutes}}</i></p>
            <h3 class="song-title">{{music.title}}</h3>

            <ul class="song-btn">
                <button class="iconfont icon-prev"></button>
                <button :class="{ 'iconfont icon-play' : !isPlaying,'iconfont icon-pause': isPlaying}"
                        @click="togglePlay">
                    <!-- {{isPlaying}}-->
                </button>
                <button class="iconfont icon-next"></button>
            </ul>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import {
        Indicator
    } from 'mint-ui'
    export default {
        data() {
            return {
                isPlaying: false,
                currentIndex: 0, // 
                currentTime: 0, //
                durationTime: 0, //
                playTime: 0,
                newLyric: {}, //
                lyric: {
                    "lyric": "[00:00.00]  : \n[00:01.00]"
                },
                top: 200,
                list: {},
                music: {
                    title: '~~',
                    author: '~~',
                    url: '',
                    pic: '',
                }
            }
        },
        beforeCreate() {
            // console.info("-----------:beforeCreate----------------");
        },
        created() {
            // console.info("-----------:cereted ----------------");
            //this.ajaxLyric();//

        },
        mounted() {
            this.get();
            this.$emit("music", this.music);
            // console.info("---------------:mounted--------------------");
            this.audioEvent();
            this.ajaxLyric(); //

        },
        //keep-alive
        activated() {
            this.ajaxLyric(); //
            this.currentTime = 0;
            this.get();
            // console.info("------------: activated----------");

        },
        methods: {
            audioEvent: function() {
                var audio = document.querySelector('#audio');
                var _this = this;

                // (:iosaudio)
                audio.addEventListener("loadedmetadata", function() {
                    audio.play();
                    _this.isPlaying = true;
                    Indicator.close();
                    console.info("");
                });

                //
                audio.addEventListener("timeupdate", function() {
                    _this.currentTime = this.currentTime;
                });

                //
                audio.addEventListener("ended", function() {
                    console.log("");
                    _this.isPlaying = false;
                });
            },
            loading: function() {
                Indicator.open({
                    text: '...',
                    spinnerType: 'fading-circle'
                });
            },
            //
            togglePlay: function() {
                var audio = document.querySelector('#audio');
                if (this.isPlaying == false) {
                    audio.play();
                    this.isPlaying = true;
                } else {
                    audio.pause();
                    this.isPlaying = false;
                }
            },
            //
            prev: function() {

            },
            //
            next: function() {

            },

            get: function() {
                var id = this.$route.query.id;
                var _this = this;
                this.loading();
                var url = "http://m.kugou.com/app/i/getSongInfo.php?cmd=playInfo&hash=" + id + "&from=mkugou"
                axios.get(bird + url).then(function(res) {
                    // console.log(res.data.data);
                    // _this.list = res.data;


                    this.list = res.data
                    _this.playTime = this.list.timeLength;

                    _this.music = {
                        title: _this.list.songName,
                        author: _this.list.fileName,
                        url: _this.list.url,
                        pic: (_this.list.imgUrl).replace('{size}', 400),
                    };
                    Indicator.close();

                    this.$emit('title', this.music.title);
                }.bind(this)).catch(function(error) {
                    console.log(error)
                })
            },
            //
            ajaxLyric: function() {
                let id = this.$route.query.id;
                this.newLyric = {}
                let url = "http://www.kugou.com/yy/index.php?r=play/getdata&hash=" + id;
                axios.get(bird + url)
                    .then(function(res) {
                        this.lyric.lyric = JSON.stringify(res.data.data.lyrics);
                        this.newLyric = this.parseLyric(res.data.data.lyrics);
                        this.scrollLyrics(); //

                    }.bind(this))
                    .catch(function(error) {
                        console.log(error);
                    })

            },
            //
            parseLyric: function(text) {
                var lines = text.split('\n'),
                    //[xx:xx.xx]
                    pattern = /\[\d{0,4}:\d{0,4}.\d{0,4}\]/g,
                    //
                    result = [];
                //
                while (!pattern.test(lines[0])) {
                    lines = lines.slice(1);
                }
                //'\n'
                lines[lines.length - 1].length === 0 && lines.pop();
                lines.forEach(function(v, i, a) {
                    //[xx:xx.xx]
                    var time = v.match(pattern),
                        //
                        value = v.replace(pattern, '');
                    //time[xx:xx.xx][xx:xx.xx][xx:xx.xx]
                    time.forEach(function(v1, i1, a1) {
                        //xx:xx.xx
                        var t = v1.slice(1, -1).split(':');
                        //
                        result.push([parseInt(t[0], 10) * 60 + parseFloat(t[1]), value]);
                    });
                });
                //
                result.sort(function(a, b) {
                    return a[0] - b[0];
                });

                return result;
            },
            scrollLyrics: function() {
                var audio = document.querySelector('#audio');
                var uls = document.getElementById("lyriclist");
                var lis = uls.getElementsByTagName("li");

                var _this = this;

                audio.addEventListener("timeupdate", function() {
                    for (var i = 0, l = _this.newLyric.length; i < l; i++) {
                        if (this.currentTime > _this.newLyric[i][0]) {
                            _this.top = -i * 25;
                            lis[i].className = "current";
                        }
                    }
                });
            }
        },
        components: {}
    }
</script>
<style scoped>
    #m-box {
        width: 100%;
        height: 100%;
        overflow: hidden;
        float: left;
        position: relative;
        min-height: 100%;
        _height: 100%;
    }
    
    #m-bg {
        width: 100%;
        background-repeat: no-repeat;
        position: absolute;
        height: 100%;
        min-height: 100%;
        _height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: -1;
        background-size: 100% 100%;
        background-position: center center;
        opacity: 0.5;
        -webkit-transition: opacity 0.3s;
        transition: opacity 0.3s;
        -webkit-transform: scale(1.5);
        -ms-transform: scale(1.5);
        transform: scale(1.5);
        -webkit-transform-origin: center top;
        -ms-transform-origin: center top;
        transform-origin: center top;
        clear: both;
        background-size: cover;
    }
    
    @-webkit-keyframes rotation {
        from {
            -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
        }
        to {
            -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
        }
    }
    
    .roation {
        -webkit-transform: rotate(360deg);
        animation: rotation 10s linear infinite;
        -moz-animation: rotation 10s linear infinite;
        -webkit-animation: rotation 10s linear infinite;
        -o-animation: rotation 10s linear infinite;
    }
    
    .m-circle {
        opacity: 1;
        z-index: 999;
        width: 40%;
        display: block;
        border-radius: 50%;
        margin: 0 auto;
        margin-top: 30%;
        padding-bottom: 20px;
    }
    
    .m-circle img {
        width: 100%;
        height: 100%;
        margin: 0 auto;
        border-radius: 50%;
        border: 20px solid #333;
        box-sizing: border-box;
    }
    /*audio player*/
    
    #audio-play {
        position: fixed;
        width: 100%;
        left: 0;
        padding: 20px 20px;
        bottom: 0;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        box-shadow: 3px 3px 8px #ddd;
        background: rgba(152, 152, 150, 0.7);
    }
    
    .progressbar {
        margin-top: 10px;
        width: 100%;
        height: 3px;
        background: #666;
    }
    
    #current_line {
        height: 3px;
        background: #cb5d5d
    }
    
    #audio-play p i {
        color: #333;
        font-size: 12px;
    }
    
    .song-title {
        text-align: center;
        font-size: 16px;
        font-weight: 400;
        padding: 20px 10px;
        color: #262424;
    }
    
    .song-btn button {
        width: 33.3%;
        display: inline-block;
        margin-left: -4px;
        text-align: center;
        padding: 15px 0;
        background: transparent;
        border: 0;
        outline: none;
        font-size: 30px;
        color: #262424;
    }
</style>


